<template>
    <section>
    <loader :is-visible="isLoading"></loader>
    <tg-list :IsShowAction="true" ModuleName="CompanyAdmin" SubModuleCode="CompanyAdmin" :RenderRowActionMethod="renderActions" 
    v-on:ActionButtonClick="actionButtonClick" IdentityColumn="LAYOUT_ID" :HeaderText="$t('ExternalLink')" :listType="['List']" 
    :ListData="ExternalData" :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecord" 
    :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchData" :showCheckBox="false" :SortExp="SortExp" 
    :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch" :CurrentPageNumber="PageNumber"
    v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
    </tg-list>
    <div v-if="copiedSuccess" class="toast-notification">
            URL copied to clipboard!
    </div>
    </section>
</template>

<script>
import DataService from '../../../services/DataService';
import Schema from '../CompanyAdmin/Schema';

export default {
    data() {
        return {
            test:this.$t('url_copied'),
            isLoading: false,
            ExternalData: [],
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            SortExp: '',
            SortBy: '',
            PageNumber: 1,
            PageSize: 10,
            Search: '',
            externalUrl: '',
            storeFrontUrl: "http://{subdomain}.com",
            copiedSuccess: false,
            Headers: [{
                    COLUMN_NAME: "MODULE_NAME",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Name",
                    DISPLAY_ORDER: 1
                },
                {
                    COLUMN_NAME: "EXTERNAL_LINK",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "URL",
                    DISPLAY_ORDER: 2
                }
            ]
        };
    },
    async created() {
        var vm = this;
        await vm.FetchData();
    },
    methods: {
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        commonHeaderButtonClick: function (event) {},
        actionButtonClick: function (callbackfunction, item) {
            var vm = this;
            switch (callbackfunction) {
                case 'ExternalLink':
                    vm.ExternalLink(item);
                    break;
                case 'ClickOnView':
                    vm.ViewLink(item);
                    break;
                case 'Edit':
                    vm.Edit(item.MODULE_NAME);
                    break;
            }
        },
        ExternalLink: function (item) {
            var vm = this;
            // Copy the EXTERNAL_LINK from the row to the clipboard
            if (item && item.EXTERNAL_LINK) {
                navigator.clipboard.writeText(item.EXTERNAL_LINK)
                    .then(() => {
                        vm.copiedSuccess = true; // Show success message
                        // Hide the success message after 3 seconds
                        setTimeout(() => {
                            vm.copiedSuccess = false;
                        }, 3000);
                    })
                    .catch(err => {
                        console.error('Failed to copy URL: ', err);
                        // Optionally show an error message
                    });
            }
        },
        ViewLink: function (item) {
            // Open the EXTERNAL_LINK in a new tab
            if (item && item.EXTERNAL_LINK) {
                window.open(item.EXTERNAL_LINK, '_blank');
            }
        },
        Edit: function (moduleName) {
            if (moduleName === 'Appointment') {
                return '/Appointment/GlobalURL?requestFrom=ExtLinks';
            } else if (moduleName === 'JobPost') {
                return '/JobPost/ManageLayout?requestFrom=ExtLinks';
            } else {
                return '/Ticketing/ManageLayout?requestFrom=ExtLinks';
            }
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];

            self.listActions.forEach(actionItem => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {
                    case 'ExternalLink':
                        action.isVisible = true; 
                        break;
                    case 'ClickOnView':
                        action.isVisible = true; 
                        break;
                    case 'Edit':
                        if (row.MODULE_NAME.toLowerCase() == 'hiring') {
                            action.isVisible = true;
                            action.isDisabled = true;
                        } else {
                            action.isVisible = true;
                            action.isDisabled = false;
                        }
                        break;
                    default:
                    action.isVisible = true; 
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        FetchData: async function () {
            var vm = this;
            vm.isLoading = true;
            var url = `search=${vm.Search}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}`;
            await DataService.GetExternalLinkList(url).then(response => {
                vm.ExternalData = [];
                if (response.data != '') {
                    vm.ExternalData = response.data;
                }
            });
            vm.isLoading = false;
        }
    }
}
</script>
<style>
/* Basic styling for the toast notification */
.toast-notification {
    position: fixed;
    bottom: 20px;
    right: 20px;
    background-color: #4caf50;
    color: white;
    padding: 10px 20px;
    border-radius: 5px;
    z-index: 1000;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}
</style>